header {
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem calc(1.5% + 1rem) 1rem 1.5%;
  width: 100%;
  z-index: 30;
  box-sizing: border-box;
  backdrop-filter: blur(6px);
  background-color: rgba(33, 54, 69, 0.6);
}
.header-buttons {
  display: flex;
  gap: 2rem;
}